import { useState, useEffect } from "react";
import { Round } from "@/lib/models/candidate";
import { X } from "lucide-react";
import { updateInterviewRound } from "@/api/interviews/InterviewRounds";

export default function InterviewForm({
  className,
  round,
  roundNo,
}: {
  className?: string;
  round: Round;
  roundNo: number;
}) {
  // States for form fields
  const [interviewDate, setInterviewDate] = useState(round.roundDate || "");
  const [interviewerName, setInterviewerName] = useState(round.interviewerName || "");
  const [status, setStatus] = useState(round.interviewStatus || ""); // Either 'passed' or 'rejected'
  const [remarks, setRemarks] = useState(round.remarks || "");
  const [techRating, setTechRating] = useState(round.techRating || 0);
  const [softskillsRating, setSoftskillsRating] = useState(round.softskillsRating || 0);
  const [technologies, setTechnologies] = useState<string[]>([]);
  const [newTechnology, setNewTechnology] = useState("");

  // Add a technology to the list
  const addTechnology = () => {
    if (newTechnology && !technologies.includes(newTechnology)) {
      setTechnologies([...technologies, newTechnology]);
      setNewTechnology("");
    }
  };

  // Remove a technology
  const removeTechnology = (tech: string) => {
      setTechnologies(technologies.filter((t) => t !== tech));
    
  };

  // Form submission handler
  const updateRound = async (e: React.FormEvent) => {
    e.preventDefault();

    const updatedData = {
      interviewDate,
      interviewerName,
      status,
      remarks,
      techRating,
      softskillsRating,
      technologyInterviewed: technologies,
    };

    await updateInterviewRound(roundNo, updatedData);
  };

  return (
    <div className={`${className}`}>
      <div className="w-full max-w-2xl mx-auto p-6">
        <h1 className="text-2xl font-semibold mb-6">Update Round</h1>

        <form className="space-y-6" onSubmit={updateRound}>
          <div className="space-y-2">
            <label htmlFor="interview-date" className="block text-sm font-medium">
              Interview Date
            </label>
            <input
              type="date"
              id="interview-date"
              value={interviewDate}
              onChange={(e) => setInterviewDate(e.target.value)}
              className="w-full px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="space-y-2">
            <label htmlFor="interviewer-name" className="block text-sm font-medium">
              Interviewer Name
            </label>
            <input
              type="text"
              id="interviewer-name"
              value={interviewerName}
              onChange={(e) => setInterviewerName(e.target.value)}
              className="w-full px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="space-y-2">
            <span className="block text-sm font-medium">Interview Status</span>
            <div className="flex gap-4">
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  name="status"
                  value="passed"
                  checked={status === "passed"}
                  onChange={() => setStatus("passed")}
                  className="form-radio text-blue-600"
                />
                <span className="ml-2">Passed</span>
              </label>
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  name="status"
                  value="rejected"
                  checked={status === "rejected"}
                  onChange={() => setStatus("rejected")}
                  className="form-radio text-blue-600"
                />
                <span className="ml-2">Rejected</span>
              </label>
            </div>
          </div>

          <div className="space-y-2">
            <label htmlFor="remarks" className="block text-sm font-medium">
              Remarks
            </label>
            <textarea
              id="remarks"
              value={remarks}
              onChange={(e) => setRemarks(e.target.value)}
              className="w-full px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 min-h-[100px]"
            />
          </div>

          {round.techRating !== undefined && (
            <div className="space-y-2">
              <label htmlFor="tech-rating" className="block text-sm font-medium">
                Tech Rating
              </label>
              <input
                type="number"
                id="tech-rating"
                value={techRating}
                onChange={(e) => setTechRating(Number(e.target.value))}
                min="1"
                max="10"
                className="w-full px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          )}

          {round.softskillsRating !== undefined && (
            <div className="space-y-2">
              <label htmlFor="soft-skill-rating" className="block text-sm font-medium">
                Soft Skill Rating
              </label>
              <input
                type="number"
                id="soft-skill-rating"
                value={softskillsRating}
                onChange={(e) => setSoftskillsRating(Number(e.target.value))}
                min="1"
                max="10"
                className="w-full px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          )}

          {round.technologyInterviewed && (
            <div className="space-y-4">
              <label className="block text-sm font-medium">Technologies Interviewed</label>
              <div className="flex gap-2">
                <input
                  type="text"
                  placeholder="Enter Single or Multiple Technologies"
                  className="flex-grow px-3 py-2 bg-gray-50 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={newTechnology}
                  onChange={(e) => setNewTechnology(e.target.value)}
                />
                <button
                  type="button"
                  className="px-6 py-2 bg-gray-200 text-gray-700 rounded-md hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-500"
                  onClick={addTechnology}
                >
                  Add
                </button>
              </div>

              {technologies.length>0 && (
                <div className="space-y-2">
                  <span className="block text-sm font-medium">Technologies Added</span>
                  <div className="flex flex-wrap gap-2">
                    {technologies.map((tech:string) => (
                      <span
                        key={tech}
                        className="inline-flex items-center bg-gray-200 text-gray-700 rounded-full px-3 py-1 text-sm"
                      >
                        {tech}
                        <button
                          type="button"
                          onClick={() => removeTechnology(tech)}
                          className="ml-2 hover:bg-gray-300 rounded-full p-0.5"
                        >
                          <X className="h-3 w-3" />
                        </button>
                      </span>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}

          <button
            type="submit"
            className="w-full py-2 px-4 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          >
            Update
          </button>
        </form>
      </div>
    </div>
  );
}
